* {
  margin: 0;
  padding: 0;
  /*手机页面默认字体颜色*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  -webkit-overflow-scrolling: touch;
}
html,
body {
  width: 100%;
  height: 100%;
  color: #212529;
}
html {
  overflow-x: hidden;
}
a {
  color: #333333;
  text-decoration: none !important;
}
textarea {
  resize: none;
  /*禁止文本框可滑动改变大小*/
}
a,
input,
button,
textarea,
select,
option {
  outline: none;
  -webkit-tap-highlight-color: #000000;
  /*取消选中高亮*/
